var APP = {

    Player: function() {
        //画面WEBGL新建
        var loader = new THREE.ObjectLoader();

        this.getObjLoader = function() {
            return loader;
        };
        this.objParse = function(obj) {
            return loader.parse(obj);
        };
        this.width = 500;
        this.height = 500;

        this.initContent = function() {
            window.renderer = new THREE.WebGLRenderer({
                antialias: true
            });
            window.renderer.setPixelRatio(window.devicePixelRatio);
            window.renderer.outputEncoding = THREE.sRGBEncoding;
            window.renderer.domElement.setAttribute("tabindex", -1);
            window.container = document.getElementById("threejsContent");
            window.container.appendChild(window.renderer.domElement);
        };

        this.loadRenderSetting = function(json) {

            var project = json.project;

            if (project.vr !== undefined) window.renderer.xr.enabled = project.vr;
            if (project.shadows !== undefined) window.renderer.shadowMap.enabled = project.shadows;
            if (project.shadowType !== undefined) window.renderer.shadowMap.type = project.shadowType;
            if (project.toneMapping !== undefined) window.renderer.toneMapping = project.toneMapping;
            if (project.toneMappingExposure !== undefined) window.renderer.toneMappingExposure = project.toneMappingExposure;
            if (project.physicallyCorrectLights !== undefined) window.renderer.physicallyCorrectLights = project.physicallyCorrectLights;

            // this.setScene(loader.parse(json.scene));
            // this.setCamera(loader.parse(json.camera));

        };

        this.setCamera = function(value) {
            window.camera = value;
            window.camera.aspect = this.width / this.height;
            window.camera.updateProjectionMatrix();

        };
        this.loadJsonScene = function(sceneJson) {
            this.setScene(loader.parse(sceneJson));
        };

        this.loadCustomerScene = function() {
            window.scene = new THREE.Scene();
            return window.scene;
        };

        this.loadCustomerCamera = function() {
            //透视投影照相机
            let cameraPerspective = new THREE.PerspectiveCamera(45, this.width / this.height, 1, 2000);
            cameraPerspective.position.set(100, 100, 100);
            cameraPerspective.updateProjectionMatrix();
            window.camera = cameraPerspective;
            return window.camera;
        };

        this.loadJsonCamera = function(cameraJson) {
            this.setCamera(loader.parse(cameraJson));
        };
        this.setScene = function(value) {

            window.scene = value;

        };

        this.setSize = function(width, height) {
            this.width = width;
            this.height = height;
            if (window.camera) {
                var aspect = this.width / this.height;
                window.camera.aspect = aspect;
                window.camera.left = -height * aspect;
                window.camera.right = height * aspect;
                window.camera.top = height;
                window.camera.bottom = -height;
                window.camera.updateProjectionMatrix();
            }
            if (window.renderer) {
                window.renderer.setSize(width, height);
            }
        };

        function animate() {
            // renderer.clearDepth();
            // renderer.clear();
            window.renderer.render(window.scene, window.camera);
        }

        this.play = function() {
            animate();
        };

        this.setAnimate = function(animateCall) {
            window.renderer.setAnimationLoop(animateCall);
        };

        this.stop = function() {


            window.renderer.setAnimationLoop(null);

        };

        this.dispose = function() {

            window.renderer.dispose();

            window.camera = undefined;
            window.scene = undefined;

        };

    }

};

export {
    APP
};
