var movePosition = {
    x: 0,
    y: 0,
    z: 0
};
var lookAtPosition = {
    x: 0,
    y: 0,
    z: 0
};
var isCurrent3D = true;
var defaultCount = 40;

function setCurrentView(current) {
    this.isCurrent3D = current;
}

function setMoveTo(po) {
    movePosition = po;
}
//设定相机移动坐标
function setCameralPosition(position, count, lookAt) {
    if (position) {
        this.cacleMovePosition(position, count);
        if (lookAt) {
            lookAtPosition = lookAt;
        } else {
            lookAtPosition = { x: 0, y: 0, z: 0 };
        }
    } else {
        let defaultPosition = {
            x: -1.2808716578107804,
            y: 7.362963388536753,
            z: 66.23047186757788
        };
        if (this.isCurrent3D) {
            //3D
            this.setCameralPosition(defaultPosition, count);
            lookAtPosition = { x: 0, y: 0, z: 0 };
        } else {
            //2.5D的时候，不做画面视角重置
        }
    }
}

function cacleMovePosition(disPosition, count) {
    let c;
    if (count) {
        c = count;
    } else {
        c = defaultCount;
    }
    movePosition = {
        x: disPosition.x - window.camera.position.x,
        y: disPosition.y - window.camera.position.y,
        z: disPosition.z - window.camera.position.z,
    };
    movePosition.dist = disPosition;
    movePosition.stepx = movePosition.x / c;
    movePosition.stepy = movePosition.y / c;
    movePosition.stepz = movePosition.z / c;
    movePosition.count = 0;
    movePosition.defaultCount = c;
}

function moveCamera() {
    if (movePosition.stepx || movePosition.stepy || movePosition.stepz) {
        if (movePosition.count == movePosition.defaultCount) {
            movePosition = { x: 0, y: 0, z: 0 };
            lookAtPosition = { x: 0, y: 0, z: 0 };
            return
        }
        if (movePosition.oldPosition) {
            // //坐标信息变动，重新计算
            // if ((Math.round(window.camera.position.x) - Math.round(movePosition.stepx)) > Math.round(movePosition.oldPosition.x) ||
            //     (Math.round(window.camera.position.y) - Math.round(movePosition.stepy)) > Math.round(movePosition.oldPosition.y) ||
            //     (Math.round(window.camera.position.z) - Math.round(movePosition.stepz)) > Math.round(movePosition.oldPosition.z)) {
            //     cacleMovePosition(movePosition.dist, movePosition.defaultCount);
            //     console.log("坐标偏移，重新计算坐标");
            // }
            if (movePosition.oldPosition.x != window.camera.position.x || movePosition.oldPosition.y != window.camera.position.y || movePosition.oldPosition.z != window.camera.position.z) {
                window.camera.position.set(movePosition.oldPosition.x, movePosition.oldPosition.y, movePosition.oldPosition.z);
                console.log("坐标偏移，重新计算坐标");
            }
        }

        window.camera.position.x = window.camera.position.x + movePosition.stepx;
        window.camera.position.y = window.camera.position.y + movePosition.stepy;
        window.camera.position.z = window.camera.position.z + movePosition.stepz;
        window.controls.target.set(lookAtPosition.x, lookAtPosition.y, lookAtPosition.z);
        // window.camera.lookAt(lookAtPosition.x, lookAtPosition.y, lookAtPosition.z);
        movePosition.x = movePosition.x - movePosition.stepx;
        movePosition.y = movePosition.y - movePosition.stepy;
        movePosition.z = movePosition.z - movePosition.stepz;
        movePosition.stepx = movePosition.stepx;
        movePosition.stepy = movePosition.stepy;
        movePosition.stepz = movePosition.stepz;
        movePosition.count = movePosition.count + 1;
        movePosition.dist = movePosition.dist;
        movePosition.defaultCount = movePosition.defaultCount;
        movePosition.oldPosition = { x: window.camera.position.x, y: window.camera.position.y, z: window.camera.position.z };
        return
    }
    lookAtPosition = { x: 0, y: 0, z: 0 };
    movePosition = { x: 0, y: 0, z: 0 };
}
export default {
    setCameralPosition,
    cacleMovePosition,
    setMoveTo,
    moveCamera,
    setCurrentView
}
