var cameraA, cameraB, cameraArray;
var cameraTextureLoader;
var recorderCameraArray = [];
var recorderCameraArray_T = [];
var cameraGuildArray = [];
var material_recorder_warning, material_recorder_normal, geometry_recorder_com, texture_recorder_warning, texture_recorder_normal;
var buildingImageArray = [];
var material_building_door, material_building_camera, geometry_building_com, texture_building_door, texture_building_camera;

