function showBuildingGuild() {
    for (let o in ModelData.baseMstData.buildingGuildData) {
        loadBuildingImage(ModelData.baseMstData.buildingGuildData[o]);

    }
}

function clearBuildingGuilds() {
    for (let i in buildingImageArray) {
        window.scene.remove(buildingImageArray[i]);
    }
    buildingImageArray = [];
}


function loadBuildingImage2(textWord, position) {
    //用canvas生成图片
    let canvas = document.getElementById("textHiddenCanvas");
    let ctx = canvas.getContext("2d");
    //制作矩形
    // ctx.fillStyle = "rgba(255,165,0,0.8)";
    // ctx.fillRect(0, 0, 100, 40);
    //设置文字
    // ctx.font="6px Georgia";
    ctx.font = "18px microsoft yahei"; //设置画笔的字体大小和格式
    // ctx.fillText(textWord, 0, 20);
    // for (let i in textWord) {
    //     ctx.fillText(textWord[i], 0, i * 18);
    // }
    var img = new Image;
    img.src = "/static/fbx/BIM/camera/camera_1_normal.png";
    img.onload = function() {
        ctx.drawImage(img, 0, 0, 128, 128);
        for (let i in textWord) {
            ctx.fillText(textWord[i], 0, i * 18);
        }
        //生成图片
        let url = canvas.toDataURL("image/png");

        //将图片构建到纹理中
        let geometry1 = new THREE.PlaneGeometry(1, 1);
        let textureLoader = new THREE.TextureLoader();
        let texture = textureLoader.load(url);
        texture.encoding = 3001;
        let material1 = new THREE.MeshBasicMaterial({
            map: texture,
            side: THREE.DoubleSide,
            opacity: 1,
            transparent: true
        });
        let rect = new THREE.Mesh(geometry1, material1);
        rect.position.set(position.x, position.y, position.z);
        buildingImageArray.push(rect);
        window.scene.add(rect);
    }
}

function loadBuildingImage(data) {
    if (cameraTextureLoader == undefined) {
        cameraTextureLoader = new THREE.TextureLoader();
    }
    //将图片构建到纹理中
    if (geometry_building_com == undefined) {
        geometry_building_com = new THREE.PlaneGeometry(3, 3);
    }

    if (material_building_door == undefined) {
        texture_building_door = cameraTextureLoader.load(
            "/static/fbx/BIM/camera/camera_1_normal.png"
        );
        material_building_door = new THREE.MeshBasicMaterial({
            map: texture_building_door,
            side: THREE.DoubleSide,
            opacity: 1,
            color: new THREE.Color(0xffffff),
            transparent: true
        });
        material_building_door.map.encoding = 3001;
    }
    if (material_building_camera == undefined) {
        texture_building_camera = cameraTextureLoader.load(
            "/static/fbx/BIM/camera/camera_1_warning.png"
        );
        material_building_camera = new THREE.MeshBasicMaterial({
            map: texture_building_camera,
            side: THREE.DoubleSide,
            opacity: 1,
            color: new THREE.Color(0xffffff),
            transparent: true
        });
        material_building_camera.map.encoding = 3001;
    }

    let rect;
    if (data.type == 1) {
        rect = new THREE.Mesh(geometry_building_com, material_building_camera);
    } else {
        rect = new THREE.Mesh(geometry_building_com, material_building_door);
    }
    rect.name = data.name;
    rect.position.set(data.position.x, data.position.y, data.position.z);
    buildingImageArray.push(rect);
    window.scene.add(rect);
}

function getBuildingGuildArray() {
    return buildingImageArray;
}
var cameraGuildNames = [];

function getCameraGuildNames() {
    // console.log(cameraGuildNames);
    return cameraGuildNames;
}

function showCameraGuild() {
    cameraGuildArray = [];
    let data = ModelData.webcameraData[0].data;
    for (let i in data) {
        loadCameraGuild(data[i]);
        cameraGuildNames.push(data[i].name + "_guild");
    }
    return cameraGuildArray;
}

function clearCameraGuild() {
    let clearItem = [];
    for (let i in cameraGuildArray) {
        window.scene.remove(cameraGuildArray[i]);
        clearItem.push(cameraGuildArray[i]);
    }
    cameraGuildNames = [];
    cameraGuildArray = [];
    return clearItem;
}

function loadCameraGuild(data) {
    let rect;
    if (cameraTextureLoader == undefined) {
        cameraTextureLoader = new THREE.TextureLoader();
    }
    //将图片构建到纹理中
    if (geometry_recorder_com == undefined) {
        geometry_recorder_com = new THREE.PlaneBufferGeometry(0.8, 0.9, 1, 1);
    }

    if (material_recorder_warning == undefined) {
        texture_recorder_warning = cameraTextureLoader.load(
            "/static/fbx/BIM/camera/camera_1_warning.png"
        );
        material_recorder_warning = new THREE.MeshStandardMaterial({
            map: texture_recorder_warning,
            opacity: 1,
            color: new THREE.Color(0xffffff),
            transparent: true,
            emissive: new THREE.Color(0xffffff),
            emissiveMap: texture_recorder_warning
        });
        material_recorder_warning.map.encoding = 3001;
    }
    if (material_recorder_normal == undefined) {
        texture_recorder_normal = cameraTextureLoader.load(
            "/static/fbx/BIM/camera/camera_1_normal.png"
        );
        material_recorder_normal = new THREE.MeshStandardMaterial({
            map: texture_recorder_normal,
            opacity: 1,
            color: new THREE.Color(0xffffff),
            transparent: true,
            emissive: new THREE.Color(0xffffff),
            emissiveMap: texture_recorder_normal
        });
        material_recorder_normal.map.encoding = 3001;
    }
    //相机位置确认
    if (data.cyline) {
        for (let t in data.cyline) {
            if (data.cyline[t].rotation) {
                //过滤重复摄像机位置
                continue
            }
            //创建摄像机提醒
            // if (data.type == 1) {
            //     rect = new THREE.Mesh(geometry_recorder_com, material_recorder_warning);
            // } else {
            rect = new THREE.Mesh(geometry_recorder_com, material_recorder_normal);
            // }

            cameraGuildArray.push(rect);
            rect.name = data.name + "_guild";
            rect.userData.cameraPosition = data.cyline[t].camera_location;
            rect.userData.lookAtPosition = data.position;
            rect.userData.isCameraGuildModel = true;
            rect.position.set(data.cyline[t].position.x, data.cyline[t].position.y + 1, data.cyline[t].position.z);
            window.scene.add(rect);
        }
    }
}

function loadInsideCamera(data) {
    let rect;
    if (cameraTextureLoader == undefined) {
        cameraTextureLoader = new THREE.TextureLoader();
    }
    //将图片构建到纹理中
    if (geometry_recorder_com == undefined) {
        geometry_recorder_com = new THREE.PlaneBufferGeometry(0.5, 0.6, 1, 1);
    }

    if (material_recorder_warning == undefined) {

        texture_recorder_warning = cameraTextureLoader.load(
            "/static/fbx/BIM/camera/camera_1_warning.png"
        );
        material_recorder_warning = new THREE.MeshStandardMaterial({
            map: texture_recorder_warning,
            opacity: 1,
            color: new THREE.Color(0xffffff),
            transparent: true
        });
        material_recorder_warning.map.encoding = 3001;
    }
    if (material_recorder_normal == undefined) {
        texture_recorder_normal = cameraTextureLoader.load(
            "/static/fbx/BIM/camera/camera_1_normal.png"
        );
        material_recorder_normal = new THREE.MeshStandardMaterial({
            map: texture_recorder_normal,
            opacity: 1,
            color: new THREE.Color(0xffffff),
            transparent: true
        });
        material_recorder_normal.map.encoding = 3001;
    }

    //创建摄像机提醒
    if (data.type == 1) {
        rect = new THREE.Mesh(geometry_recorder_com, material_recorder_warning);
    } else {
        rect = new THREE.Mesh(geometry_recorder_com, material_recorder_normal);
    }

    recorderCameraArray.push(rect);
    rect.name = data.name;
    rect.position.set(data.position.x, data.position.y, data.position.z);
    window.scene.add(rect);
}

function loadImageWebCamera(data) {
    let rect;
    //判断相机种类
    if (data.type == 1) {
        //球机
        rect = getCameraA();
    } else {
        // 枪机
        rect = getCameraB();
    }
    //创建摄像机支架
    if (data.cyline) {
        for (let t in data.cyline) {
            //创建摄像机架子
            let cy = new THREE.CylinderBufferGeometry(data.cyline[t].radius_top, data.cyline[t].radius_bottom, data.cyline[t].height);
            let st = new THREE.MeshStandardMaterial();
            let cyline = new THREE.Mesh(cy, st);
            cyline.position.set(data.cyline[t].position.x, data.cyline[t].position.y, data.cyline[t].position.z);
            //大支架的旋转过滤
            if (data.cyline[t].rotation) {
                cyline.rotation.set(data.cyline[t].rotation.x, data.cyline[t].rotation.y, data.cyline[t].rotation.z);
            }
            //添加到屏幕
            window.scene.add(cyline);
            recorderCameraArray_T.push(cyline);
        }
    }
    //相机旋转
    if (data.rotation) {
        rect.rotation.set(data.rotation.x, data.rotation.y, data.rotation.z);
    }
    //内存数据保存
    recorderCameraArray.push(rect);
    recorderCameraArray_T.push(rect);
    //相机名字设定
    rect.name = data.name;
    rect.position.set(data.position.x, data.position.y, data.position.z);
    window.scene.add(rect);
}

function getCameraB() {
    //创建新的相机
    let geometry = new THREE.BufferGeometry().copy(cameraB.geometry);
    let material = [];
    for (let i in cameraB.material) {
        material.push(cameraB.material[i].clone());
    }
    let newMesh = new THREE.Mesh(geometry, material);
    newMesh.scale.set(0.001, 0.001, 0.001);
    return newMesh;
}

function getCameraA() {
    //创建新的相机
    let geometry = new THREE.BufferGeometry().copy(cameraA.geometry);
    let material = [];
    for (let i in cameraA.material) {
        material.push(cameraA.material[i].clone());
    }
    let newMesh = new THREE.Mesh(geometry, material);
    newMesh.scale.set(0.001, 0.001, 0.001);
    return newMesh;
}


function followCamera() {
    for (var i in cameraGuildArray) {
        cameraGuildArray[i].lookAt(window.camera.position);
    }

    for (var i in buildingImageArray) {
        buildingImageArray[i].lookAt(window.camera.position);
    }
}

function getRecorderCameraArray() {
    return recorderCameraArray;
}

function getRecorderCameraArrayT() {
    return recorderCameraArray_T;
}

function showAllInsideCamera(input) {
    recorderCameraArray = [];
    recorderCameraArray_T = [];
    let data = input;
    for (let i in data) {
        // ModelData.baseMstData.webcameraNames.push(data[i].name);
        loadInsideCamera(data[i]);
    }
    return recorderCameraArray;
}

function showAllOuterCamera(input) {
    recorderCameraArray = [];
    recorderCameraArray_T = [];
    let data = [];
    if (Array.isArray(input)) {
        data = input;
    } else {
        data = ModelData.webcameraData[0].data;
    }
    for (let i in data) {
        // ModelData.baseMstData.webcameraNames.push(data[i].name);
        loadImageWebCamera(data[i]);
    }
    return recorderCameraArray;
}

function getFbxCamer(data) {
    let item;
    if (data.type == 1) {
        item = loadOrDraw.copyMesh(cameraA, data.type);
    } else if (data.type == 2) {
        item = loadOrDraw.copyMesh(cameraB, data.type);
    }
    item.name = data.name;
    ModelData.baseMstData.webcameraNames.push(data.name);
    item.position.set(data.position.x, data.position.y, data.position.z);
    console.log(item);
    loadOrDraw.loadText("Warning", {
        x: data.position.x,
        y: data.position.y + 1,
        z: data.position.z
    });
    return item;
}

function initFbx(callback) { //相机
    if (cameraA && cameraB) {
        if (callback) {
            callback();
        }
        return
    }
    loadOrDraw.loadFBXModel(
        "/static/fbx/BIM/camera/camera.FBX",
        function(object) {
            var scale = 0.001;
            cameraA = object.getObjectByName("camera_1");
            cameraA.scale.set(scale, scale, scale);
            cameraB = object.getObjectByName("camera_2");
            cameraB.scale.set(scale, scale, scale);
            if (callback) {
                callback();
            }
        },
        false
    );
}

