var indexedDB = window.indexedDB || window.mozIndexedDB || window.webkitIndexedDB || window.msIndexedDB;

if (indexedDB === undefined) {

    console.warn('Storage: IndexedDB not available.');
    // return { init: function () {}, get: function () {}, set: function () {}, clear: function () {} };

}

var name = 'QinghuaPark'; //数据库名
var version = 1; //数据库版本

var database;


// //创建数据库
// var request = indexedDB.open(name, version);
// request.onupgradeneeded = function (event) {
//     //数据库升级操作
//     var db = event.target.result;
//     if (db.objectStoreNames.contains('states') === false) {
//         db.createObjectStore('states');
//     }
// };
// request.onsuccess = function (event) {
//     //数据库创建成功
//     database = event.target.result;
// };
// request.onerror = function (event) {
//     //数据库创建失败
//     console.error('IndexedDB', event);
// };


var indexedDBManager = {

    init: function(callbackSuccess, callbackError) {
        var request = indexedDB.open(name, version);
        request.onupgradeneeded = function(event) {
            var db = event.target.result;
            if (db.objectStoreNames.contains('states') === false) {
                db.createObjectStore('states');
            }
        };
        request.onsuccess = function(event) {
            database = event.target.result;
            callbackSuccess();
        };
        request.onerror = function(event) {
            console.error('IndexedDB', event);
            callbackError();
        };
    },

    /**
     * 数据获取(通过指定的key)
     * @param {*} key       key
     * @param {*} callback  回调方法
     * @param {*} content
     */
    get: function(key, callback, content) {

        var transaction = database.transaction(['states'], 'readwrite');
        var objectStore = transaction.objectStore('states');
        var request = objectStore.get(key);
        request.onsuccess = function(event) {
            callback(event.target.result, content);

        };

    },
    /**
     * 数据添加
     * @param {*} data  存储的数据
     * @param {*} key   存储数据的key
     */
    set: function(data, key) {
        var start = performance.now();

        var transaction = database.transaction(['states'], 'readwrite');
        var objectStore = transaction.objectStore('states');
        var request = objectStore.add(data, key);

        request.onsuccess = function() {
            console.log('[' + /\d\d\:\d\d\:\d\d/.exec(new Date())[0] + ']', 'Saved state to IndexedDB. ' + (performance.now() - start).toFixed(2) + 'ms');

        };
        request.onerror = function(event) {
            //数据添加失败
            console.error('IndexedDB', event);

        };
    },

    /**
     * 后台socket信息存储
     * @param {*} data
     */
    setSocketInfo: function(data) {
        var start = performance.now();

        var transaction = database.transaction(['states'], 'readwrite');
        var objectStore = transaction.objectStore('states');
        //获取当前db中存储的socketInfo
        var getRequest = objectStore.get("socketInfo")
        getRequest.onsuccess = function(event) {
            var alreadyExistedData = []

            if (event.target.result) {
                //已存在信息
                alreadyExistedData = event.target.result;
            }
            alreadyExistedData.push(data)

            var setRequest = objectStore.put(alreadyExistedData, "socketInfo");

            setRequest.onsuccess = function() {
                console.log('[' + /\d\d\:\d\d\:\d\d/.exec(new Date())[0] + ']', 'Saved state to IndexedDB. ' + (performance.now() - start).toFixed(2) + 'ms');

            };
        };


    },


    /**
     * 更新数据
     * @param {*} data  更新后的数据
     * @param {*} key   key
     */
    update: function(data, key) {
        var start = performance.now();

        var transaction = database.transaction(['states'], 'readwrite');
        var objectStore = transaction.objectStore('states');
        var request = objectStore.put(data, key);

        request.onsuccess = function() {
            console.log("data update success")
        };
    },

    /**
     * 数据删除
     * @param {*} key  删除数据对应的kty
     */
    delete: function(key) {
        var start = performance.now();

        var transaction = database.transaction(['states'], 'readwrite');
        var objectStore = transaction.objectStore('states');
        var request = objectStore.delete(key);

        request.onsuccess = function() {
            console.log("data delete success")
        };
    },

    /**
     * 清空states表
     */
    clear: function() {

        if (database === undefined) return;

        var transaction = database.transaction(['states'], 'readwrite');
        var objectStore = transaction.objectStore('states');
        var request = objectStore.clear();
        request.onsuccess = function() {

            console.log('[' + /\d\d\:\d\d\:\d\d/.exec(new Date())[0] + ']', 'Cleared IndexedDB.');

        };

    },

    /**
     * 数据库关闭
     */
    close() {
        //关闭数据库
        database.close();
    },

    /**
     * 数据库删除
     */
    deleteDb() {
        //关闭数据库
        database.close();
        //删除数据库
        var request = indexedDB.deleteDatabase(name);
        request.onsuccess = function(event) {
            console.log('DB delete success')
        };
        request.onerror = function(event) {
            console.error('IndexedDB', event);
        };
    },
    
    
    /**
     * 判断数据库是否存在
     */
    hasDatabase(){
        if(database){
            return true
        }
        return false
    }



};



export default indexedDBManager