function loadArm(data) {
    var loader = new THREE.AMFLoader();
    loader.load("./models/amf/rook.amf", function(meshObjects) {
        console.log(meshObjects);
        meshObjects.traverse(function(child) {
            if (child.isMesh) {
                //材质
                child.material.color = newItemBaseMaterial.color;
                child.name = data.name;
                objects.push(child);
                newContentBack.push(child);
            }
        });
        newItem.push(meshObjects);
        meshObjects.position.x = data.position.x;
        meshObjects.position.y = data.position.y;
        meshObjects.position.z = data.position.z;
        scene.add(meshObjects);
    });
}

function getBuildingTextureLoader(pictureUrl) {
    var currentMap = textureLoader.load(pictureUrl);
    var material = new THREE.MeshBasicMaterial({
        map: currentMap
    });
    material.transparent = true;
    material.blending = THREE["CustomBlending"];
    material.blendSrc = THREE["SrcAlphaFactor"];
    material.blendDst = THREE["SrcAlphaFactor"];
    material.blendEquation = THREE.AddEquation;
    material.color.set(0x705555);
    return material;
}

function getManager() {
    var manager = new THREE.LoadingManager();
    manager.onProgress = function(item, loaded, total) {
        // console.log(item, loaded, total);
    };
    return manager;
}

function loadFBXModel(modelPath, dataCallback, showLoading, progressCallback) {
    var loading;
    if (showLoading != false) {
        loading = window.LoadingService.showOnDom("#content-left");
    }

    // model
    var manage = getManager();
    var loader = new FBXLoader(manage);
    // var loader = new FBXLoader(manage);

    // var loader = new THREE.FBXLoader(manager);
    loader.load(
        modelPath,
        function(object) {
            dataCallback(object);
        },
        function(xhr) {
            if (xhr.lengthComputable) {
                var percentComplete = (xhr.loaded / xhr.total) * 100;
                console.log(Math.round(percentComplete, 2) + "% downloaded");
                if (percentComplete == 100) {
                    if (loading) {
                        loading.close();
                    }
                }
                if (progressCallback != null) {
                    progressCallback(Math.round(percentComplete, 2));
                }
            }
        },
        function(xhr) {
            console.log(xhr);
        }
    );
}

