function loadBufferSync(context, names, baseReq, callback) {
    for (let u in names) {
        let url = baseReq.url + baseReq.code + "/" + baseReq.version + "/" + name[u] + ".json";
        context.$worker
            .run(
                (req) => {
                    //异步下载
                    var xhr = new XMLHttpRequest();
                    xhr.responseType = "arraybuffer"; //返回数据流
                    xhr.open(
                        "GET",
                        url,
                        false
                    );
                    xhr.send();
                    postMessage(xhr.response);
                }, [url]
            )
            .then(result => {
                //同步
                if (callback) {

                    callback(result, names[u]);
                }
            }).catch(e => {
                callback(null, baseReq.code + "_" + names[u], versionUp);
            });
    }
}

function loadJsonSync(context, names, baseReq, versionUp, callback) {
    for (let u in names) {
        let url = baseReq.url + "/" + baseReq.code + "/" + names[u] + ".json";
        context.$worker
            .run(
                (req, version) => {
                    //异步下载
                    var xhr = new XMLHttpRequest();
                    xhr.open(
                        "GET",
                        req,
                        false
                    );
                    xhr.send();
                    console.log(req)
                    let res = JSON.parse(xhr.responseText);
                    res.version = version;
                    postMessage(res);
                }, [url, baseReq.version]
            )
            .then(result => {
                callback(result, baseReq.code + "_" + names[u], versionUp);
            }).catch(e => {
                callback(null, baseReq.code + "_" + names[u], versionUp);
            });
    }
}


