var raycaster, groundMesh;

function setPointRay(ray, ground) {
    raycaster = ray;
    groundMesh = ground;
}
/**
 * 2d 坐标切3d坐标
 * @param position
 * @returns {*}
 */
function change2dTo3d(position) {
    // var cur = {};
    // cur.x = (position.x / renderer.domElement.clientWidth) * 2 - 1;
    // cur.y = -(position.y / renderer.domElement.clientHeight) * 2 + 1;
    raycaster.setFromCamera(position, window.camera);
    var intersects = raycaster.intersectObject(groundMesh);
    if (intersects.length > 0) {
        return {
            x: intersects[0].point.x,
            y: intersects[0].point.y,
            z: intersects[0].point.z
        };
    } else {
        return {
            x: 0,
            y: 0,
            z: 0
        };
    }
}
/**
 * 3d坐标转2d坐标
 * @param world_vector
 * @returns {{x: number, y: number}}
 */
function change3dTO2d(world_vector) {
    let vector = world_vector.project(camera);
    let halfWidth = window.innerWidth / 2,
        halfHeight = window.innerHeight / 2;
    return {
        x: Math.round(vector.x * halfWidth + halfWidth),
        y: Math.round(-vector.y * halfHeight + halfHeight)
    };
}
/**
 * 获取模型位置
 * @param child
 * @returns {{x: *, y: *, z: *}}
 */
function getModelBoundingPoint(child) {
    var centroid = new THREE.Vector3();
    centroid.addVectors(
        child.geometry.boundingBox.min,
        child.geometry.boundingBox.max
    );
    centroid.multiplyScalar(0.5);
    centroid.applyMatrix4(child.matrixWorld);
    return {
        x: centroid.x,
        y: centroid.y,
        z: centroid.z
    };
}

