// Load jeep model using the AssimpJSONLoader
/**
 *
 * @param object mesh
 * @param point  {x:1,y:1,z:1}
 * @param rotate  Math.PI/4
 */
function rotateMesh(object, point, rotate) {
    var axis = new THREE.Vector3(point.x, point.y, point.z); //向量axis
    object.rotateOnAxis(axis, rotate);
}

function loadJeep() {
    new THREE.AssimpJSONLoader().load(
        "models/assimp/jeep/jeep.assimp.json",
        function(object) {
            object.scale.multiplyScalar(0.7);
            object.position.set(0, 0, 50);
            object.rotateOnAxis(new THREE.Vector3(0, 1, 0), Math.PI / 2);
            console.log(object);
            object.name = "jeepCar";
            scene.add(object);
        }
    );
}

var curveArray3d = [];
var curveArray2d = [];

function addLinePoint2DTest(p) {
    curveArray2d.push(p);
    console.log(p);
}

function addLinePoint3DTest() {
    curveArray3d.push(
        new THREE.LineCurve3(
            new THREE.Vector3(0, 0, 50),
            new THREE.Vector3(-80, 0, 50)
        )
    );
    curveArray3d.push(
        new THREE.LineCurve3(
            new THREE.Vector3(-80, 0, 50),
            new THREE.Vector3(-80, 0, -50)
        )
    );
    curveArray3d.push(
        new THREE.LineCurve3(
            new THREE.Vector3(-80, 0, -50),
            new THREE.Vector3(80, 0, -50)
        )
    );
    drawCarLine();
}

function carPointChange() {
    if (curveArray2d.length > 2) {
        for (var i in curveArray2d) {
            var p1 = change2dTo3d(curveArray2d[i]);
            var p2 = {};
            if (i == curveArray2d.length - 1) {
                p2 = change2dTo3d(curveArray2d[0]);
            } else {
                p2 = change2dTo3d(curveArray2d[parseInt(i) + 1]);
            }
            curveArray3d.push(
                new THREE.LineCurve3(
                    new THREE.Vector3(p1.x, p1.y, p1.z),
                    new THREE.Vector3(p2.x, p2.y, p2.z)
                )
            );
        }
        curveArray2d = [];
    }
}
var points = [];
var meshNeedMove = [];

function addMoveAction(meshName, x, y, z) {
    meshNeedMove.push({
        meshName: meshName,
        move: {
            x: x,
            y: y,
            z: z
        }
    });
}

function moveLocation() {
    while (meshNeedMove.length > 0) {
        var item = meshNeedMove.pop();
        var data = window.scene.getObjectByName(item.meshName);
        data.position.x = item.move.x;
        data.position.y = item.move.y;
        data.position.z = item.move.z;
    }
}

function drawCarLine() {

    carPointChange();

    var min = 1;
    for (var t in curveArray3d) {
        var dis = curveArray3d[t].v1.distanceTo(curveArray3d[t].v2);
        if (dis > 0) {
            points = points.concat(curveArray3d[t].getPoints(Math.round(dis / min)));
        }
    }
    //清除队列
    curveArray3d = [];

    if (points.length == 0) {
        console.log("there is no line data .");
        return;
    }

    var _material = new THREE.LineBasicMaterial({
        color: 0xff0000,
        opacity: 0.35
    });
    var _geometry = new THREE.BufferGeometry();
    var _pointsBuf = [];
    var _vertices = new Float32Array(_pointsBuf);
    _geometry.addAttribute("position", new THREE.BufferAttribute(_vertices, 3));
    var _lineA = new THREE.Line(_geometry, _material);
    scene.add(_lineA);
    //  addPointArray.push(_lineA);

    let _i = 0;
    var index = setInterval(() => {
        _i++;
        if (_i > points.length - 1) {
            window.clearInterval(index);
            points = [];
        } else {
            _pointsBuf.push(points[_i].x, points[_i].y, points[_i].z);
            _vertices = new Float32Array(_pointsBuf);
            _geometry.addAttribute(
                "position",
                new THREE.BufferAttribute(_vertices, 3)
            );
            addMoveAction("customer_car", points[_i].x, points[_i].y, points[_i].z);
        }
    }, 10);
}

