var textMeshArray = [];

function loadText(textWord, position) {
    //用canvas生成图片
    let canvas = document.getElementById("textHiddenCanvas");
    let ctx = canvas.getContext("2d");
    //制作矩形
    ctx.fillStyle = "rgba(255,165,0,0.8)";
    ctx.fillRect(0, 0, 100, 40);
    //设置文字
    ctx.fillStyle = "#fbfbff";
    // ctx.font="6px Georgia";
    ctx.font = 'normal 16px ';
    ctx.fillText(textWord, 0, 20);
    //生成图片
    let url = canvas.toDataURL("image/png");

    //将图片构建到纹理中
    let geometry1 = new THREE.PlaneGeometry(2, 2);
    let textureLoader = new THREE.TextureLoader();
    let texture = textureLoader.load(url);

    // canvas.width = width;
    // canvas.height = height;
    // var ctx = canvas.getContext('2d');
    // ctx.fillStyle = '#C3C3C3';
    // ctx.fillRect(0, 0, width, height);
    // ctx.font = 50 + 'px " bold';
    // ctx.fillStyle = '#2891FF';
    // ctx.textAlign = 'center';
    // ctx.textBaseline = 'middle';
    // ctx.fillText(text, width / 2, height / 2);

    let material1 = new THREE.MeshBasicMaterial({
        map: texture,
        side: THREE.DoubleSide,
        opacity: 1,
        transparent: true
    });

    let rect = new THREE.Mesh(geometry1, material1);
    rect.position.set(position.x, position.y, position.z);
    textMeshArray.push(rect);
    window.scene.add(rect);
}

function handleScene(data) {
    window.scene.uuid = data.uuid;
    window.scene.name = data.name;
    window.scene.background = (data.background !== null) ? data.background.clone() : null;
    if (data.fog !== null) window.scene.fog = data.fog.clone();
    window.scene.userData = JSON.parse(JSON.stringify(data.userData));
}

function copyMesh(org, type) {
    let geometry = new THREE.BufferGeometry();
    geometry.copy(org.geometry);
    let material;
    if (Array.isArray(org.material)) {
        material = [];
        for (let t in org.material) {
            var tmpM = new THREE.MeshPhongMaterial();
            tmpM.copy(org.material[t]);
            if (type == 2) {
                //  tmpM.color.set(0xfba306);
            }
            material.push(tmpM);
        }
    } else {
        material = new THREE.MeshPhongMaterial();
        material.copy(org.material[t]);
        if (type == 2) {
            // tmpM.color.set(0xfba306);
        }
    }
    let item = new THREE.Mesh(geometry, material);
    item.scale.set(org.scale.x, org.scale.y, org.scale.z);
    item.up.set(org.up.x, org.up.y, org.up.z);
    item.quaternion.set(
        org.quaternion.w,
        org.quaternion.x,
        org.quaternion.y,
        org.quaternion.z
    );
    item.rotation.set(org.rotation.x, org.rotation.y, org.rotation.z);
    return item;
}

function copyMeshGroup(org) {
    let group = new THREE.Group();
    if (Array.isArray(org.material)) {
        for (let t in org.material) {
            let geometry = new THREE.BufferGeometry();
            geometry.copy(org.geometry);
            let child = new THREE.Mesh(geometry, org.material[t].clone());
            group.add(child);
        }
    } else {
        let geometry = new THREE.BufferGeometry();
        geometry.copy(org.geometry);
        let child = new THREE.Mesh(geometry, org.material.clone());
        group.add(child);
    }
    group.scale.set(org.scale.x, org.scale.y, org.scale.z);
    group.up.set(org.up.x, org.up.y, org.up.z);
    group.quaternion.set(
        org.quaternion.w,
        org.quaternion.x,
        org.quaternion.y,
        org.quaternion.z
    );
    group.rotation.set(org.rotation.x, org.rotation.y, org.rotation.z);
    return group;
}
