        searchCompanyInfo() {
            if (this.searchCompany) {
                this.appearCompany = []
                for (let info of this.enterpriseList) {
                    console.log(info)
                    if (info.name.indexOf(this.searchCompany) != -1) {
                        this.appearCompany.push(info)
                    }
                }
            } else {
                this.appearCompany = this.enterpriseList
            }
        },
        dialogShowCompanyInfo(info) {
            this.companyPortraitDialog = true;
            this.iconActionBuilding = false;
            this.companyInfosDialog = false;
            this.companyIntroductionTitle = "企业画像"

            this.companyInfo.name = info.name
            this.companyInfo.type = info.type
            this.companyInfo.LegalPerson = info.legalPerson
            this.companyInfo.portrait = info.portrait
        },
        companyPortraitDialogClosed() {
            this.iconActionBuilding = true;
            this.showCompanyList = false;
            this.searchCompany = ""
        },
