        companyDetail(index, item) {
            this.companyIntroductionTitle = "企业画像"
            this.showCompanyList = false
            this.companyInfo = item
            this.companyInfo.LegalPerson = item.legalPerson
        },
        otherCompanyDetail(type) {
            this.companyIntroductionTitle = "企业列表"
            this.showCompanyList = true

            //查询相关企业
            this.appearCompany = []
            if (type) {
                if (this.buildCompany) {
                    for (let column of type) {
                        for (let company of this.buildCompany) {
                            if (company.data) {
                                for (let info of company.data) {
                                    if (info.type == column) {
                                        this.appearCompany.push(info)
                                    }
                                }
                            }
                        }
                    }
                }
            } else {
                //获取所有企业列表
                if (this.buildCompany) {
                    for (let column of type) {
                        for (let company of this.buildCompany) {
                            if (company.data) {
                                for (let info of company.data) {
                                    if (info != "建设中") {
                                        this.appearCompany.push(info)
                                    }

                                }
                            }
                        }
                    }
                }
            }

