        iconModelModeChange() {
            //   console.log(this.modelChangeFlag)
            let l = LoadingService.showOnDom("#content-left");

            if (this.modelChangeFlag == 1) {
                //白天
                scene.remove(particles)
                window.baitian_light.visible = true;
                // window.night_light_new.visible = false;
                window.night_light.visible = false;
                window.water.visible = true;
                let item = scene.getObjectByName("Logo");
                for (let o in item.material) {
                    if (item.material[o].name == "Da_zi") {
                        item.material[o].emissive.set(0x000000);
                    }
                }
            } else {
                //黑夜
                scene.add(particles);
                window.baitian_light.visible = false;
                window.night_light.visible = true;
                // window.night_light_new.visible = true;
                window.water.visible = false;

                let item = scene.getObjectByName("Logo");
                for (let o in item.material) {
                    if (item.material[o].name == "Da_zi") {
                        item.material[o].emissive.set(0xffffff);
                    }
                }
            }
            if (l) {
                l.close();
            }
