        //↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑菜单操作↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑
        //↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓模型的显示与隐藏↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓
        restoreAndClearScene() {
            if (!this.isCurrentShow3D) {
                return;
            }
            //存储模型项目
            for (let i in scene.children) {
                window.sceneStore.push(scene.children[i]);
            }
            //清除模型显示内容
            for (let t in window.sceneStore) {
                scene.remove(window.sceneStore[t]);
            }
        },
        reShowScene() {
            if (window.sceneStore == undefined || window.sceneStore.length == 0) {
                return;
            }

            //删除2.5D相关内容
            for (let i in storeFloorMesh) {
                scene.remove(storeFloorMesh[i]);
            }
            storeFloorMesh = [];

            for (let t in window.sceneStore) {
                scene.add(window.sceneStore[t]);
            }
            window.sceneStore = [];
        },
        //↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑模型的显示与隐藏↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑
        //↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓2.5D地图的显示↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓
        modelButtonSelect() {
            //重置flag
            this.setCurrentShow3D(true);
            this.reShowScene();
            CameraControll.setCameralPosition();
            this.resetControl();
        },
        change2dView(value) {
            //隐藏相机类表
            if (this.iconDeviceWebcamera) {
                this.iconMethodActionWebcamera()
            }

            //loading
            var loading = window.LoadingService.showOnDom("#content-left");
            // LayerUtils.alert(value);
            //隐藏dialog
            this.floorSelectDialog = false;

            //记录当前选择的楼层
            this.currentBuildingSelectData.selectFloor = value;

            //画面3D模型保存
            that.restoreAndClearScene();

            //3D画面flag制御
            this.setCurrentShow3D(false);

            //删除旧的2.5D相关内容
            for (let i in storeFloorMesh) {
                scene.remove(storeFloorMesh[i]);
            }

            scene.background = new THREE.Color(0x636060);
            //显示新模型
            loadOrDraw.getTexture(
                this.currentBuildingSelectData.mapData[value].url,
                function (texture) {

                    texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
                    texture.repeat.set(1, 1);
                    texture.anisotropy = 100;
                    texture.encoding = THREE.sRGBEncoding;
                    let geometry = new THREE.PlaneBufferGeometry(250, 100);

                    let material = new THREE.MeshLambertMaterial({
                        map: texture,
                        transparent: true,
                        opacity: 1,
                        color: 0xffffff,
                    });

                    let floorMesh = new THREE.Mesh(geometry, material);
                    floorMesh.rotation.set(-0.1 * Math.PI, 0, 0);
                    // ground.position.y = -0.3;
                    floorMesh.receiveShadow = true;
                    floorMesh.name = "floor-001";
                    //存储楼层的3D对象
                    storeFloorMesh.push(floorMesh);
                    scene.add(floorMesh);

                    //重置camera位置，2.5D专用位置
                    camera.position.set(0, 20, 200);
                    CameraControll.setCameralPosition({
                        x: 0,
                        y: 20,
                        z: 120,
                    }, 40, {
                        x: 0,
                        y: 0,
                        z: 0
                    });
                    if (loading) {
                        loading.close();
                    }
                },
                function () {
                    if (loading) {
                        loading.close();
                    }
                }
            );

            let timer = setTimeout(function () {
                clearTimeout(timer);
                // //加载摄像头信息
                let webC =
                    that.currentBuildingSelectData.mapData[
                        that.currentBuildingSelectData.selectFloor
                    ].webcamera;
                if (webC && webC.length > 0) {
                    let cas = webcameraUtil.showAllInsideCamera(webC);
                    for (let p in cas) {
                        //模型放大
                        cas[p].scale.set(10, 10, 10);
                        storeFloorMesh.push(cas[p]);
                    }
                }
            }, 1000);

            //control限制
            this.resetControl(true);
            //添加灯光
            this.initLight();
            // //loading框消除
            // if (loading) {
            //     loading.close();
            // }
        },
        // 2.5D地图的灯光
        initLight() {
            //环境光
            var ambient = new THREE.AmbientLight(0xffffff, 1);
            storeFloorMesh.push(ambient);
            scene.add(ambient); //将环境光添加到场景中

            // 平行光
            let directionalLight = new THREE.DirectionalLight();
            directionalLight.position.set(30, 30, 0);
            directionalLight.intensity = 0.5;
            directionalLight.castShadow = true;
            directionalLight.shadow.mapSize.width = 100;
            directionalLight.shadow.mapSize.height = 100;

            // 为光线设置阴影属性
            directionalLight.shadow.camera.left = -50;
            directionalLight.shadow.camera.right = 50;
            directionalLight.shadow.camera.top = 50;
            directionalLight.shadow.camera.bottom = -50;
            directionalLight.shadow.camera.far = 300;

            // 偏差率
            directionalLight.shadow.bias = -0.001;
            storeFloorMesh.push(directionalLight);
            scene.add(directionalLight);
        },
