        //↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓室外监控摄像机加载↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓
        initWebCameramodels() {
            webcameraNames = [];
            webcameraUtil.initFbx(function () {

                //只在安防页面加载摄像机
                if (that.currentRouteType == 1) {
                    let webCArray = webcameraUtil.getRecorderCameraArrayT();
                    if (webCArray.length == 0) {
                        let cp0 = webcameraUtil.showAllOuterCamera();
                        objects = objects.concat(cp0);
                        for (let p in cp0) {
                            webcameraNames.push(cp0[p].name);
                        }
                    } else {
                        let cp1 = webcameraUtil.getRecorderCameraArrayT();
                        for (let o in cp1) {
                            cp1[o].visible = true;
                        }
                        let cp2 = webcameraUtil.getRecorderCameraArray();
                        for (let p in cp2) {
                            webcameraNames.push(cp2[p].name);
                        }
                    }

                } else {
                    let webCArray = webcameraUtil.getRecorderCameraArrayT();
                    for (let o in webCArray) {
                        webCArray[o].visible = false;
                    }
                }
            })

        },
        //↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑室外监控摄像机加载↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑
        //↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓模型异步加载↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓
        getModelJson(dbIsOk) {
            that.modelTotalCount = ModelData.baseMstData.jsonTreeModelName.length + ModelData.baseMstData.jsonBuildingModelName.length;
            //db数据取得-在线数据取得
            that.dbDataCheck(ModelData.baseMstData.jsonTreeModelName, dbIsOk, function (obj, name, type) {
                if (obj != null) {
                    //加载路边绿化的回调处理
                    let mesh = threeApp.objParse(obj);
                    if (mesh.name == "night_light_all") {
                        mesh.visible = false;
                        window.night_light = mesh;
                    } else if (mesh.name == "Group_light_baitian") {
                        window.baitian_light = mesh;
                    }
                    scene.add(mesh);
                }
                //DB更新
                that.dbDataPush(obj, name, type)
            });

            //新建建筑组
            var buildingMeshGroup = new THREE.Group();
            buildingMeshGroup.rotation.set(-1.5707963267948963, 0, 0);
            buildingMeshGroup.position.set(-0.000038, 8.111278, -0.000008);
            buildingMeshGroup.name = "Tsinghua_Science_park";
            scene.add(buildingMeshGroup);
            //全局设定建筑物
            window.building = buildingMeshGroup;

            //db数据取得-在线数据取得
            that.dbDataCheck(ModelData.baseMstData.jsonBuildingModelName, dbIsOk, function (obj, name, type) {
                if (obj != null) {
                    //加载建筑物的回调处理
                    let mesh = threeApp.objParse(obj);
                    buildingMeshGroup.add(mesh);
                    if (mesh.type == "Group") {
                        mesh.traverse(function (child) {
                            if (child.isMesh) {
                                //数据存储
                                if (
                                    ModelData.baseMstData.excluedByClickName.indexOf(child.name) < 0
                                ) {
                                    objects.push(child);
                                }
                            }
                        });
                    } else {
                        objects.push(mesh);
                        mesh.material.vertexColors = false;
                    }
                    if ("humian" == mesh.name) {
                        //水波
                        let tLoader = new THREE.TextureLoader();
                        //16,8
