        dbDataPush(obj, name, type) {
            if (obj != null) {
                // (versionUp=> 0:更新  1:新增  2:不做DB处理 )
                if (name != undefined) {
                    if (type == 0) {
                        //更新
                        indexedDBManager.update(obj, name);
                    } else if (type == 1) {
                        //新增
                        indexedDBManager.set(obj, name);
                    } else if (type == 2) {
                        //DB 初期化失败，不做DB处理
                    }
                }
            }

            this.modelLoadCount++;
            this.loadingProgress = Math.ceil(this.modelLoadCount * (100 / that.modelTotalCount));
            if (this.modelLoadCount >= that.modelTotalCount) {
                that.isModelLoading = false;

                //设定相加的位置
                this.initCameraStartPosition();
            }
        },
        dbDataCheck(data, dbIsOk, loadCallback) {
            //DB状态确认
            if (dbIsOk) {

                //建筑物遍历
                for (let s in data) {

                    //DB取得
                    indexedDBManager.get(that.jsonModelInfo.code + "_" + data[s], function (obj) {
                        if (obj) {
                            if (obj.version == that.jsonModelInfo.version) {
                                //DB数据直接取得
                                loadCallback(obj);
                            } else {
                                //DB数据更新(versionUp=> 0:更新  1:新增  2:不做DB处理 )
                                loadOrDraw.loadJsonSync(that, [data[s]], that.jsonModelInfo, 0, loadCallback);
                            }
                        } else {
                            //建筑物worker拉取
                            loadOrDraw.loadJsonSync(that, [data[s]], that.jsonModelInfo, 1, loadCallback);
                        }
                    });
                }
            } else { //建筑物worker拉取
                loadOrDraw.loadJsonSync(that, data, that.jsonModelInfo, 2, loadCallback);
            }
        },
