        //初期化建筑物上的视频
        initVideo() {
            for (let i in ModelData.baseMstData.videoLedMeshData) {
                let data = ModelData.baseMstData.videoLedMeshData[i];

                //添加立方体
                var geometry = new THREE.BoxBufferGeometry(
                    data.videoSize.x,
                    data.videoSize.y,
                    data.videoSize.z
                );

                //获取到video对象
                var video = document.getElementsByClassName(data.name);
                // video.muted = true;
                // video[0].setAttribute("src", data.url);
                //通过video对象实例化纹理
                var texture = new THREE.VideoTexture(video[0]);
                texture.wrapS = texture.wrapT = THREE.ClampToEdgeWrapping;
                texture.minFilter = THREE.LinearFilter;
                var material = new THREE.MeshBasicMaterial({
                    map: texture,
                });
                var mesh = new THREE.Mesh(geometry, material);
                mesh.position.set(data.position.x, data.position.y, data.position.z);
                scene.add(mesh);
                video[0].play();
            }
        },
