                dataPerpare.buildingNames.push(cData.name);

                //初期重置楼层数据
                ModelData.baseMstData.buildingsSelect[o].floor = [];
                //遍历楼层信息
                for (let i in cData.mapData) {
                    ModelData.baseMstData.buildingsSelect[o].floor.push(
                        cData.mapData[i].name
                    );
                }
            }
