        onDocumentMouseDown(event) {
            if (
                this.companyInfosDialog ||
                this.companyInfoCarouselDialog ||
                this.floorSelectDialog ||
                this.webCameraDialog
            ) {
                //清除楼层选择框
                this.floorSelectDialog = false;
                //清除按钮状态
                this.iconViewTop = false;
                this.iconViewTop = false;
                this.iconViewLeft = false;
                this.iconViewRight = false;
                //清除选中的建筑
                this.cleanSelectObjects();
                return;
            }
            let clickAreaLimit = [0, 0];
            // var renderer=mouseRenderer
            //通过鼠标点击的位置计算出raycaster所需要的点的位置，以屏幕中心为原点，值的范围为-1到1.

            mouse.x =
                ((event.clientX - container.getBoundingClientRect().left) /
                    container.offsetWidth) *
                2 -
                1;
            mouse.y = -(
                    (event.clientY - container.getBoundingClientRect().top) /
                    container.offsetHeight
                ) *
                2 +
                1;

            //   mouse.x = (event.clientX / renderer.domElement.clientWidth) * 2 - 1;
            // mouse.y = -(event.clientY / renderer.domElement.clientHeight) * 2 + 1;
            //console.log("2d -x :" + event.clientX + " y:" + event.clientY);

            // 通过鼠标点的位置和当前相机的矩阵计算出raycaster
            raycaster.setFromCamera(mouse, camera);

            // 获取raycaster直线和所有模型相交的数组集合

            let rayObjs = objects;
            if (storeFloorMesh.length > 0) {
                rayObjs = storeFloorMesh;
            }
            var intersects = raycaster.intersectObjects(rayObjs);
            // loadOrDraw.addLinePoint2DTest({ x: mouse.x, y: mouse.y });
            //将所有的相交的模型的颜色设置为红色，如果只需要将第一个触发事件，那就数组的第一个模型改变颜色即可
            if (intersects.length > 0) {
                //打印选择的组
                // if (intersects[0].object.parent.type == "Group") {
                //   console.log("=========start==========");
                //   intersects[0].object.parent.traverse(function(child) {
                //     console.log(child.name);
                //   });
                //   console.log("==========end=========");
                // }

                //var tmp = scene.getObjectByName(intersects[0].object.name);
                //console.log(tmp);
                //物体移动
                //transformControls.attach(tmp);

                // window.layer.msg(" name:" + intersects[0].object.name, {
                //   offset: "tl",
                //   skin: "customer-msg-01"
                // });

                // LayerUtils.msg(" name:" + intersects[0].object.name, "customer-msg-01");

                //2.5D的场合，点击事件监听，弹出企业信息或者摄像头监控
                if (!this.isCurrentShow3D) {
                    //2.5D场合的企业信息
                    let r = this.customerClickBy2D(intersects);

                    if (!r) {
                        //楼层内部的摄像机
                        if (storeFloorMesh.length > 0) {
                            //当前数据取得
                            let fd = this.currentBuildingSelectData.mapData[
                                this.currentBuildingSelectData.selectFloor
                            ];

                            //索引位置取得
                            let tp = -1;
                            for (let z in fd.webcamera) {
                                if (fd.webcamera[z].name == intersects[0].object.name) {
                                    tp = z;
                                    break;
                                }
                            }
                            if (tp >= 0) {
                                //相机数据赋值
                                this.webCameraSelectData = fd.webcamera[tp];
                                //数据更新后显示
                                this.$nextTick(function () {
                                    this.webCameraDialog = true;
                                });
                            } else {
                                // console.error("摄像机坐标数据不对。");
                            }
                        }
                    }
                } else {
                    var selectedObject = intersects[0].object;
                    //3D场合
                    //摄像机点击过滤
                    //需要区分楼外部，和楼内部 TODO
                    let index = webcameraNames.indexOf(selectedObject.name);
                    if (index >= 0) {
                        // 2.5D摄像机监控播放
