                        CameraControll.setCameralPosition(selectedObject.userData.cameraPosition, 40, selectedObject.userData.lookAtPosition);
                    }

                    if (this.iconActionBuilding) {
                        //3D场合的选中
                        if (selectedObject.geometry.type == "BufferGeometry") {
                            //建筑物选择,包含非建筑过滤
                            this.addSelectedLine(intersects);
                        }
                    }

                }

                console.log("name :" + intersects[0].object.name);
                console.log(intersects[0]);
            } else {
