            }

            //console.log(loadOrDraw.change2dTo3d(mouse));
        },
        //2.5D地图的点击事件
        customerClickBy2D(intersects) {
            let result = false;
            //2.5D地图点击过滤
            if (this.currentBuildingSelectData.mapData.length > 0) {
                let floorData = this.currentBuildingSelectData.mapData[
                    this.currentBuildingSelectData.selectFloor
                ].company;
                for (let g in floorData) {
                    if (floorData[g].position) {
                        if (
                            intersects[0].point.x > floorData[g].position.x[0] &&
                            intersects[0].point.x < floorData[g].position.x[1]
                        ) {
                            if (
                                intersects[0].point.y > floorData[g].position.y[0] &&
                                intersects[0].point.y < floorData[g].position.y[1]
                            ) {
                                if (
                                    intersects[0].point.z > floorData[g].position.z[0] &&
                                    intersects[0].point.z < floorData[g].position.z[1]
                                ) {
                                    this.companyInfoCarouselBind.pictures = floorData[g].pictures;
                                    this.companyInfoCarouselDialog = true;
                                    result = true;
                                    break;
                                }
                            }
                        }
                    }
                }
            }
            return result;
        },

        //↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓选中模型效果↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓
        //清除选中模型效果
        cleanSelectObjects() {
            for (let i in selectedObjects) {
                window.building.remove(selectedObjects[i]);
            }
            selectedObjects = [];
        },
        //添加选中模型效果
        addSelectedLine(intersects) {
            let mesh = intersects[0].object;
            this.cleanSelectObjects();

            var obj = mesh;
            let po = mesh.position;
            let currentSelectName;
            //获取类型
            if ("Tsinghua_Science_park" == mesh.parent.name) {
                currentSelectName = mesh.name;
            } else {
                currentSelectName = mesh.parent.name;
            }
            //查询数据位置
            let index = dataPerpare.buildingNames.indexOf(currentSelectName);
            //限制除建筑以外的选择事假
            if (index < 0) {
                return;
            }
            //1栋和2栋的区分
            if (currentSelectName == "1") {
                if (intersects[0].point.x < -4.5) {
                    currentSelectName = "2";
                    index = index + 1;
                }
            }
            //切换显示列表数据
            let oh = container.getBoundingClientRect().offsetHeight - 240;
            let ow = container.getBoundingClientRect().offsetHeight - 400;
            let p = dataPerpare.buildingNames.indexOf(currentSelectName);
            let f = ModelData.baseMstData.buildingsSelect[p];
            if (f) {
                this.viewCompanyData.data = []
                this.viewCompanyData.count = 0
                if (this.buildCompany) {
                    for (let info of this.buildCompany) {
                        if (f.alise == info.alise) {
                            if (info.data[0] != "建设中") {
                                this.viewCompanyData.data = info.data;
                                this.viewCompanyData.count = info.data.length;
                            }
                        }
                    }
                } else {}
                this.viewCompanyData.name = f.alise;
            }

            //取得数据 ,更新到模型中
            this.currentBuildingSelectData =
                ModelData.baseMstData.buildingsSelect[index];
            //判断建筑种类0:group  1:none group
            if (this.currentBuildingSelectData.type == 0) {
                //轮廓位置修正点
                if (this.currentBuildingSelectData.position) {
                    po = this.currentBuildingSelectData.position;
                } else {
                    po = mesh.parent.position;
                }
                obj = scene.getObjectByName(this.currentBuildingSelectData.select);
            }

            //dataPerpare.buildingNames
            //计算相机移动位置
            CameraControll.cacleMovePosition(this.currentBuildingSelectData.camera);
            //轮廓线创建
            var edges = new THREE.EdgesGeometry(obj.geometry);
            var line = new THREE.LineSegments(
                edges,
                new THREE.LineBasicMaterial({
                    color: 0x8aacf7,
                })
            );
            line.position.set(po.x, po.y, po.z);
            line.rotation.set(obj.rotation.x, obj.rotation.y, obj.rotation.z);
            line.scale.set(obj.scale.x, obj.scale.y, obj.scale.z);
            //内存数据保存
            selectedObjects.push(line);
            //添加到画面
            window.building.add(line);
            //弹出框确认
            let path = this.$route.path;
            console.log(path);
            if (path == "/home/parkSecurity") {
                //弹出楼层选择
                this.floorSelectDialog = true;
            } else {
                //弹出企业选择
                this.companyInfosDialog = true;
                this.$nextTick(function () {
                    $(".companyInfoDialogRef").scrollTop = 0;
                });
            }
        },
        //↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑室外监控摄像机加载↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑

