        //地面初期化
        initGround() {
            let textureLoader = new THREE.TextureLoader();
            let groundTexture = textureLoader.load(
                "/static/fbx/BIM/Road/grasslight-big.jpg"
            );
            groundTexture.wrapS = groundTexture.wrapT = THREE.RepeatWrapping;
            groundTexture.repeat.set(1000, 1000);
            // groundTexture.anisotropy = 100;
            groundTexture.encoding = THREE.sRGBEncoding;

            let groundGeometry = new THREE.PlaneBufferGeometry(2000, 2000);
            let groundMaterial = new THREE.MeshPhongMaterial({
                map: groundTexture,
                color: 0xffffff,
            });

            let ground = new THREE.Mesh(groundGeometry, groundMaterial);
            ground.rotation.x = -0.5 * Math.PI;
            ground.position.y = -0.5;
            ground.receiveShadow = true;
            ground.visible = true;
            ground.name = "ground-001";
            groundMesh = ground;

            //天空初期化
            cloudTexture = new THREE.TextureLoader().load(
                "/static/fbx/BIM/back_cloud.png"
            );
            cloudTexture.wrapS = cloudTexture.wrapT = THREE.RepeatWrapping;
            cloudTexture.repeat.set(1, 1);
            cloudTexture.encoding = THREE.sRGBEncoding;
            window.cloudTexture = cloudTexture;
            //随机点的计算线程
            this.$worker.run(
                () => {
                    var vertices = [];
                    while (vertices.length < 10000) {
                        let x = 2000 * (0.5 - Math.random());
                        let y = 2000 * (0.5 - Math.random());
                        let z = 2000 * (0.5 - Math.random());
                        if (Math.abs(x) < 100 || y < 50 || Math.abs(z) < 50) {
                            continue
                        }
                        vertices.push(x); // x
                        vertices.push(y); // y
                        vertices.push(z); // z
                    }
                    postMessage(vertices);
                }
            ).then(vertices => {
                //星星生成
                var geometry = new THREE.BufferGeometry();
                geometry.setAttribute('position', new THREE.Float32BufferAttribute(vertices, 3));
                particles = new THREE.Points(geometry, new THREE.PointsMaterial({
                    color: 0x888888
                }));
                window.particles = particles;
            })

            let cloudGeometry = new THREE.CylinderBufferGeometry(
                500,
                500,
                1000,
                108,
                1
            );
            let cloudMaterial = new THREE.MeshStandardMaterial({
                map: cloudTexture,
                color: 0xffffff,
                side: THREE.BackSide,
                emissive: 0x0D0D0D
            });
            backgroundCloud = new THREE.Mesh(cloudGeometry, cloudMaterial);
            backgroundCloud.scale.set(1, 1, 1);
            window.backgroundCloud = backgroundCloud;
        },

        gridGroundHelper() {
            //地面
            scene.add(groundMesh);
            // groundMesh.material.visible = false
            //白云背景
            scene.add(backgroundCloud);

            //var helper = new THREE.GridHelper(1200, 60, 0xCD3700, 0x424242);
            //scene.add(helper);
            // var cubeTextureLoader = new THREE.CubeTextureLoader();
            // // cubeTextureLoader.setPath('../../../static/fbx/BIM/sky');
            // var cubeTexture = cubeTextureLoader.load([
            //     "../../../static/fbx/BIM/sky/posx.jpg", "../../../static/fbx/BIM/sky/negx.jpg",
            //     "../../../static/fbx/BIM/sky/posy.jpg", "../../../static/fbx/BIM/sky/negy.jpg",
            //     "../../../static/fbx/BIM/sky/posz.jpg", "../../../static/fbx/BIM/sky/negz.jpg"
            // ]);
            // scene.background = cubeTexture;
        },
        //画面的渲染器
        animate() {
            // stats.update();
            webcameraUtil.followCamera();
            CameraControll.moveCamera();
            // console.log(camera.position)
            controls.update();
            renderer.render(window.scene, window.camera);
        },
        init() {
            //初期化
            threeApp = new APP.Player();

            //初期化地面资源，放置页面初期化时候的空白
            this.initGround();

            //3D模型初期化
            this.loadModel();
        },
    },
    mounted() {
        if (this.currentRouteType == 0 || this.currentRouteType == 1 || this.currentRouteType == 3) {
            //内容加载
            this.init();
            //窗口重置
            this.onWindowResize();

            window.onresize = () => {
                return (() => {
                    that.onWindowResize();
                })();
            };
            this.onWindowResize();
            this.cameraData = Mock.dataMock.cameraData;
        }
    },
    watch: {
        threeList: function () {
            console.log("tree点击数据:", this.threeList);
            this.cameraData = this.threeList;
        },
        routePath: function (path) {
            console.log("path:" + path);
            //路由变化的BIM重置
            //路由切换
            switch (this.$route.path) {
                case HOME_DASHBOARD:
                    this.currentRouteType = 0;
                    break;
                case HOME_PARK_SECURITY:
                    this.currentRouteType = 1;
                    break;
                case HOME_PARK_ENERGY:
                    this.currentRouteType = 2;
                    break;
                case HOME_PARK_INVESTMENT:
                    this.currentRouteType = 3;
                    break;
                case HOME_PARK_PROPERTY:
                    this.currentRouteType = 4;
                    break;
            }
            //判断加载状况
            if (window.building == undefined) {
                this.mounted();
            } else {
                //夜间效果还原
                if (this.modelChangeFlag == 2) {
                    this.modelChangeFlag = 1;
                    this.iconModelModeChange();
                }
                //3D状态还原
                if (!this.isCurrentShow3D) {
                    this.modelButtonSelect();
                }
                //重置视角
                this.initCameraStartPosition();

                //相机引导选择
                if (this.iconDeviceWebcamera) {
                    this.iconMethodActionWebcamera();
                }

                this.initWebCameramodels();
                //画面项目重置
                this.iconActionLarger = false;
                this.iconActionSmaller = false;
                this.iconAction2DBack = true;
                this.iconAction3DBack = false;
                this.iconViewMain = false;
                this.iconViewTop = false;
                this.iconViewLeft = false;
                this.iconViewRight = false;
                this.webCamereDataListSelectModel = "";
                this.iconDeviceDoor = false;

            }
        },
        bimDataInfo(newValue, oldValue) {
            this.buildCompany = newValue.baseMstData.buildingsSelect

            this.enterpriseList = []
            if (this.buildCompany) {
                for (let company of this.buildCompany) {
                    if (company.data) {
                        for (let info of company.data) {
                            if (info != "建设中") {
                                this.enterpriseList.push(info)
                            }
                        }
                    }
                }
            }
        }
    },
    destroyed() {
        this.worker = null;
    }, //keep-alive钩子函数：组件被激活时调用
    activated() {
        console.log('BIM被激活');
    },
    //keep-alive钩子函数：组件消失，被缓存时调用
    deactivated() {
        console.log('BIM被缓存');
    },
    beforeRouteLeave(to, from, next) {
        //设置下一个路由的meta（即首页）
        to.meta.keepAlive = true; // 让首页缓存，即不刷新
        next();
    }
};
</script>

